.development-infos {
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  margin: 40px 0;

  .warning {
    border: 1px solid red;
    color: var(--main-sheet-back-color);
    border-radius: 3px;
    span {
      color: var(--main-sheet-back-color);
    }
  }

  .monitor {
    span {
      padding: 0 3px !important;
    }

    input {
      text-align: center;
    }
  }

  .validation {
    margin-top: 8px;
  }
}

.development {
  .skills-list {
    height: 100%;
    overflow-y: auto;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    padding-top: 8px !important;

    .item {
      .item-controls {
        font-family: system-ui;
        font-size: 12px;
      }
      color: var(--main-sheet-front-color);
      display: grid;
      grid-template-columns: 15px auto 23px 23px 23px 23px 23px;
      align-items: center;

      &.pulpCharacter {
        grid-template-columns: 30px auto 23px 23px 23px 23px 23px 23px;
      }

      grid-template-rows: 16px;
      border-bottom: 1px solid var(--main-sheet-front-color);
      margin-right: 8px;

      .flagged4dev {
        color: goldenrod;
      }

      span {
        height: 16px;
        line-height: 16px;
        font-size: 0.8rem;
        color: var(--main-sheet-front-color);
      }

      input {
        height: 16px;
        line-height: 16px;
        font-size: 0.8rem;
        color: var(--main-sheet-front-color);
        background-color: rgba(0, 0, 0, 0.1);
        padding: 0 1px;
        margin: 0;
        text-align: right;
      }

      .adjustment-value {
        &.locked {
          background-color: white;
        }

        border-right: 1px solid var(--main-sheet-front-color);
        text-align: right;
      }
    }
  }
}
