@colorBlue: rgba(43, 55, 83, 1);
@borderColorBlue: rgba(43, 55, 83, 0.5);
@colorRed: rgba(103, 11, 11, 1);
@colorGreen: #234337;
@whiteBackground: rgba(255, 255, 255, 0.5);
@greyBackground: rgba(0, 0, 0, 0.1);

:root {
  --main-sheet-bg: var(--main-sheet-bg);
  --main-sheet-image: var(--main-sheet-image);
  --main-sheet-back-color: @colorRed;
  --main-sheet-front-color: @colorBlue;
  --main-sheet-interactie-color: @colorRed;
  --main-sheet-font: "";
  --main-sheet-cursive-font: "";
  --skill-length: 160px;
  --skill-specialization-length: 152px;
}

