.coc7.sheet {
  .inventory {
    height: 100%;
  }

  .inventory-list {
    list-style: none;
    margin: 0;
    padding: 0 5px;
    // overflow-y: auto;
    scrollbar-width: thin;

    // Inventory Item
    .item {
      line-height: 16px;
      padding: 0 2px; // to align with the header border
      border-bottom: 1px solid @colorFaint;
      &:last-child {
        border-bottom: none;
      }

      // Item Header Name
      .item-name {
        cursor: pointer;
        max-height: 16px;
        width: 0;
        text-overflow: ellipsis;
        overflow: hidden;
      }

      .item-image {
        flex: 0 0 16px;
        background-size: 16px;
        margin-right: 5px;
        height: 16px;
      }

      h4 {
        margin: 0;
        white-space: nowrap;
        overflow-x: hidden;
      }

      &.rollable:hover .item-image {
        background-image: var(--d20-grey) !important;
      }
      &.rollable .item-image:hover {
        background-image: var(--d20-black) !important;
      }

      i.attuned {
        color: @colorTan;
      }

      // Item uses
      .item-uses input {
        width: 24px;
        text-align: center;
      }

      // Item Dropdown Properties
      .item-properties {
        margin-top: 3px;
      }

      // Charged
      .item-recharge {
        flex: 0 0 80px;
        text-align: right;
        font-size: 11px;
        color: @colorTan;
        white-space: nowrap;
      }
    }

    // Inventory Header
    .inventory-header {
      margin: 2px 0;
      padding: 0;
      background: rgba(0, 0, 0, 0.05);
      border: @borderGroove;
      font-weight: bold;
      line-height: 15px;

      h3 {
        margin: 0 -5px 0 0;
        padding-left: 5px;
        // .modesto();
        font-size: 1.125em;
      }

      .item-controls a.item-create {
        flex: 0 0 100%;
      }
    }

    // Item Detail Sections
    .item-detail {
      flex: 0 0 70px;
      font-size: 12px;
      color: @colorTan;
      text-align: center;
      border-right: 1px solid @colorFaint;
      word-break: break-word;
      white-space: nowrap;
      overflow: hidden;

      &:last-child {
        border-right: none;
      }
      &.item-action {
        flex: 0 0 100px;
      }
    }

    .item-weight {
      flex: 0 0 60px;
      border-left: 1px solid @colorFaint;
      border-right: 1px solid @colorFaint;
    }

    .item-list {
      list-style: none;
      margin: 0;
      padding: 0;
    }

    // Item Control Buttons
    .item-controls {
      flex: 0 0 32px;
      .flexrow();
      justify-content: flex-end;
      height: 16px;

      a {
        flex: 0 0 16px;
        font-size: 10px;
        text-align: center;
        color: @colorTan;
      }
    }

    // Item Dropdown Summary
    .item-summary {
      flex: 0 0 100%;
      font-size: 12px;
      line-height: 13px;
      padding: 0.25em 0.5em;
      border-top: @borderGroove;
    }
  }
}
